<template>
  <div style="height:100%">
    <template v-if="$store.state.customerNo !== ''">
      <div class="operate-div" style="font-size: 0.94em;padding-right:0.8em">
        <selects ref="selects"  :option="selectOpt"   @getSelect="getOption"></selects>
     <!--    <selects ref="selects" v-if="showAllDevice" :option="selectDevice"   @getSelect="getDevice"></selects> -->
        <datePicker ref="timer"  type= "doubleDate"  :dateOpt="dateOpt" @getDatePicker="getOption"></datePicker>
        <!-- <button class="button-operate"   @click="getSearchTable" style="margin-right:0;">
          <i class="hasIcon"  style="margin-top: 0;"><icon name="search"  style="font-size: 1.4em;"></icon></i>
          搜索
        </button> -->
      </div>
      <div ref="myOwnTable" class="table-container" :style="{height: tableHeight || 'calc(100% - 5em)', padding:'0 0 0.5em 0', width: '100%', 'font-size': '0.94em'}">
        <tables ref="myTable" :tableOpt="TableOpt" class="tablePadding" v-if="tableShow"></tables>
      </div>
    </template>
    <template v-else>
      <div class="emptyShowPic">
        <div class="emptyContent">
          <img src="dist/img/empty-data.png">
          <p>请先输入查询条件获取对应信息</p>
        </div>
      </div>
    </template>
  </div>
</template>
<script>
  export default{
    data() {
      return {
        showAllDevice: true,
        tableHeight: '',
        tableShow: false,
        TableOpt: {
          column: [
            {title: '用户姓名', field: 'customerName', textAlign: 'center', width: '120'},
            {title: '表计户号', field: 'meterUserNo', textAlign: 'center', width: '120'},
            {title: '联系电话', field: 'customerContact', textAlign: 'center', width: '120'},
            {title: '旧表号', field: 'meterNo', textAlign: 'center', width: '150'},
            //{title: '旧表剩余金额', field: 'remainAmount', textAlign: 'center', width: '120'},
            {title: '旧表总示示数', field: 'power', textAlign: 'center', width: '120'},
            {title: '新表号', field: 'newMeterNo', textAlign: 'center', width: '150'},
          //  {title: '新表剩余金额', field: 'newRemainAmount', textAlign: 'center', width: '120'},
            {title: '新表总示示数', field: 'newPower', textAlign: 'center', width: '120'},
            {title: '用户地址', field: 'customerAddr', textAlign: 'center', width: '220'},
            {title: '换表人员', field: 'userName', textAlign: 'center', width: '100'},
            {title: '换表时间', field: 'changeTime', textAlign: 'center', width: '160'}
          ],
          url: 'comprehensive/queryChangeMeterList',
          method: 'post',
          pageSize: 20,
          pageNumber: 1,
          pageList: [20, 40, 60],
          queryParam: {deviceType: '07'}
        },
        searchDate: '',
        searchParam: {
          deviceType: '07',
          startDate: '',
          endDate: '',
          cno: '',
          meterUserNo: ''
        }
      }
    },
    computed: {
      selectDevice: function () {
        return {
          cs: 'server',
          key: 'searchParam.deviceNo',
          url: 'comprehensive/queryDeviceInfo',
          hasAll: true,
          method: 'post',
          param: {deviceType: this.searchParam.deviceType, customerNo: this.$store.state.customerNo},
           width: '12em'
        }
      },
      selectOpt: function () {
        return {
          cs: 'client',
          width: '8em',
          key: 'searchParam.deviceType',
          data: this.getEnumItem('deviceType').obj
        }
      },
      dateOpt: function () {
        let dates = this.initDate(true);
        return {
          key: 'searchDate',
          value: {startDate: dates.start, endDate: dates.end},
          width: '23em'
        }
      }
    },
    methods: {
       getDevice(val) {
        if(val.val !== '' && typeof val.val !== 'undefined') {
           let arr = val.val.split(',');
           if(arr[1] !=  this.searchParam.cno) {
               this.searchParam.meterUserNo = arr[0];
               this.searchParam.cno = arr[1];
               this.getSearchTable()
           }
        } else if(val.val === '' && this.searchParam.cno !== '') {
           this.searchParam.meterUserNo = '';
           this.searchParam.cno = '';
           this.getSearchTable();
        }
      },
      getSearchTable: function () {
        let that = this;
        that.$nextTick(function () {
          that.showAllDevice = true;
          let arr = that.getSearchData(that.searchParam);
          arr['customerNo'] = that.$store.state.customerNo;
          that.searchTable(arr, 1);
        })
      },
      initTable: function () {
         this.tableShow = false;
         let that = this;
         that.$nextTick(function () {
           that.showAllDevice = true;
           setTimeout(function() {
             let date = that.initDate(true);
            // that.searchDate = date.start + '至' + date.end;
             that.TableOpt.queryParam['startDate']  = date.start;
             that.TableOpt.queryParam['endDate']  = date.end;
             that.TableOpt.queryParam['customerNo'] = that.$store.state.customerNo;
             that.tableShow = true;
           }, 100)
         })
         
      },
      windowChange:function () {
          this.getHeight('calc(100% - 5em)');
      }
    },

    mounted: function () {
      this.initTable();
      let that = this;
      // window.onresize = function () {
      //    that.getHeight('calc(100% - 5em)');
      // }
    },
    watch: {
      'searchDate'(val) {
        let arr = this.searchDate.split('至');
        this.searchParam.startDate = arr[0];
        this.searchParam.endDate = arr[1];
        this.getSearchTable();
      },
      'searchParam.deviceType'(val) {
        this.showAllDevice = false;
        let date = this.initDate(true);
        let allDate = date.start + '至' + date.end;
        if (allDate === this.searchDate) {
          this.getSearchTable();
        } else {
            this.$refs.timer.dateOpt.value = {startDate: date.start, endDate: date.end};
            this.$refs.timer.init();
            this.searchDate = allDate;
        }
      },
       '$store.state.customerNo': {
           handler(val, oldVal) {
             if(typeof val !== 'undefined' && val !== oldVal) {
               this.showAllDevice = false;
               this.initTable();
             }
           }
        }
    }
  }
</script>

<style lang="scss">

</style>



